/**
** Application CSS or overrides (below)
**/
.icon-flipped {
  transform: scaleX(-1);
  -moz-transform: scaleX(-1);
  -webkit-transform: scaleX(-1);
  -ms-transform: scaleX(-1);
}
[v-cloak] {
  display: none;
}
.tkn-app {
  /** 
     **Boostrap overrides - applied globally
     */
}
.tkn-app body {
  background-color: #f5f8fa;
  font-weight: 400;
  font-size: 1rem;
  font-family: Open Sans, Helvetica, Arial, sans-serif;
  color: #636b6f;
}
.tkn-app a {
  color: #198e8f;
  text-decoration: none;
  cursor: pointer;
}
.tkn-app a:hover {
  color: #116364;
}
.tkn-app .table {
  color: inherit;
}
.tkn-app .page-nav {
  margin-bottom: 1em;
  background-color: #f8f9fa;
  border: 1px solid #dae0e5;
}
.tkn-app .card {
  overflow: hidden;
  margin-bottom: 22px;
  border: 1px solid #dae0e5;
}
.tkn-app .card-header {
  font-weight: 500;
  background-color: #f8f9fa;
  border-bottom: 1px solid #dae0e5;
}
.tkn-app .btn {
  text-transform: uppercase;
  font-size: inherit;
