}
.tkn-app .btn-sm,
.tkn-app .btn-group-sm > .btn {
  padding: 0.25rem 0.5rem;
  font-size: 0.875rem;
  line-height: 1.5;
  border-radius: 0.2rem;
}
.tkn-app .btn-light {
  color: inherit;
  background-color: #e6e6e6;
  border-color: #d9d9d9;
}
.tkn-app .btn-light:hover {
  background-color: #d9d9d9;
