}
.tkn-app .btn-light:focus,
.tkn-app .btn-light.focus {
  background-color: #d9d9d9;
  border-color: #d9d9d9;
  box-shadow: 0 0 0 0.2rem rgba(216, 217, 219, 0.5);
}
.tkn-app .btn-light:not(:disabled):not(.disabled):active,
.tkn-app .btn-light:not(:disabled):not(.disabled).active {
  color: inherit;
  background-color: #cccccc;
  border-color: #cccccc;
}
.tkn-app .btn-light:not(:disabled):not(.disabled):active:focus,
.tkn-app .btn-light:not(:disabled):not(.disabled).active:focus {
  box-shadow: 0 0 0 0.2rem rgba(216, 217, 219, 0.5);
}
.tkn-app .btn-primary {
  background-color: #34a5dd;
  border-color: #34a5dd;
}
.tkn-app .btn-primary:hover {
  color: #fff;
  background-color: #208abe;
  border-color: #208abe;
}
.tkn-app .btn-primary:focus,
.tkn-app .btn-primary.focus {
  background-color: #208abe;
  border-color: #208abe;
  box-shadow: 0 0 0 0.2rem #b7dff3;
}
.tkn-app .btn-primary:not(:disabled):not(.disabled):active,
.tkn-app .btn-primary:not(:disabled):not(.disabled).active {
  background-color: #196a92;
  border-color: #196a92;
}
.tkn-app .btn-primary:not(:disabled):not(.disabled):active:focus,
.tkn-app .btn-primary:not(:disabled):not(.disabled).active:focus {
  box-shadow: 0 0 0 0.2rem #b7dff3;
}
.tkn-app .btn-primary.dropdown-toggle {
  background-color: #34a5dd;
}
.tkn-app .btn-primary.dropdown-toggle:hover {
  background-color: #208abe;
  border-color: #208abe;
}
.tkn-app .btn-primary.dropdown-toggle:focus {
  box-shadow: 0 0 0 0.2rem #b7dff3;
}
.tkn-app .btn-link {
  color: #34a5dd;
  padding: 0rem 0rem;
}
.tkn-app .btn-link:hover {
  color: #208abe;
}
.tkn-app .pagination-bar .page-size-list {
  margin-top: 1rem;
}
.tkn-app .pagination-bar .pages-list {
  flex-wrap: wrap;
  margin-top: 1rem;
}
.tkn-app .pagination-bar .pages-list .page-item.active .page-link {
  background-color: #34a5dd;
  border-color: #34a5dd;
  color: #fff;
}
.tkn-app .pagination-bar .pages-list .page-item.active .page-link:hover {
  color: #fff;
  background-color: #34a5dd;
}
.tkn-app .pagination-bar .pages-list .page-item .page-link {
  background-color: #fff;
  color: #34a5dd;
}
.tkn-app .pagination-bar .pages-list .page-item .page-link:hover {
  color: #34a5dd;
  background-color: #f8f9fa;
}
.tkn-app .pagination-bar .pages-list .page-item .page-link:focus {
  box-shadow: 0 0 0 0.2rem #b7dff3;
}
.tkn-app .dropdown-menu {
  font-size: inherit;
}
.tkn-app .dropdown-menu .dropdown-item {
  color: #34a5dd;
}
.tkn-app .dropdown-menu .dropdown-item.hover,
.tkn-app .dropdown-menu .dropdown-item:hover {
  background-color: #f8f9fa;
}
.tkn-app .dropdown-menu .dropdown-item.active,
.tkn-app .dropdown-menu .dropdown-item:active {
  text-decoration: none;
  background-color: #e9ecef;
}
.tkn-app .form-control {
  color: inherit;
}
.tkn-app .form-control:focus {
  border-color: #208abe;
  outline: 0;
  box-shadow: 0 0 0 0.2rem #b7dff3;
}
.tkn-app .tkn-list-group .tkn-list-group-item {
  background-color: #e9ecef;
  border-bottom: 1px solid #b7dff3;
}
.tkn-app .tkn-list-group .tkn-list-group-item > div {
  margin-top: auto;
  margin-bottom: auto;
}
