.documents-upload .dropbox {
  outline: 2px dashed white;
  /* the dash box */
  outline-offset: -10px;
  background: #34a5dd;
  color: white;
  padding: 10px 10px;
  min-height: 200px;
  /* minimum height */
  position: relative;
  cursor: pointer;
}
.documents-upload .dropbox .input-file {
  opacity: 0;
  /* invisible but it's there! */
  width: 100%;
  height: 200px;
  position: absolute;
  cursor: pointer;
}
.documents-upload .dropbox p {
  font-size: 2em;
  text-align: center;
  padding: 50px 0;
}
.documents-upload .dropbox:hover {
  background: #76c2e8;
  /* when mouse over to the drop zone, change color */
}
.documents-upload .uploads-container {
  width: 100%;
}
.documents-upload .uploads-container .uploads.table .large-file {
  color: red;
}
.documents-upload .uploads-container .uploads.table .remove {
  max-width: 20px;
}
.documents-upload .uploads-container .uploads.table .file-size {
  max-width: 60px;
}
.documents-upload .uploads-container .uploads.table .file-name {
  max-width: 100px;
}
.documents-upload .uploads-container .uploads.table .file-name span {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  display: inline-block;
  max-width: 100%;
}
.documents-upload .uploads-container .uploads.table .title input {
  width: 100%;
}
.documents-management .documents-search .search-bar {
  margin-bottom: 15px;
  display: flex;
}
.documents-management .documents-search .search-bar .buttons {
  margin-left: 0.5rem;
  min-width: fit-content;
}
.documents-management .documents-list .popper {
  color: inherit;
  background-color: #fff;
  border-radius: 5px;
  box-shadow: rgba(102, 96, 96, 0.74) 0 0 5px 0;
  padding-left: 5px;
  padding-right: 5px;
}
.documents-management .documents-list .item .preview {
  width: 20px;
  text-align: center;
}
.documents-management .documents-list .item .title {
  max-width: 150px;
}
.documents-management .documents-list .item .title .title-container {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  display: inline-block;
  max-width: 100%;
}
.documents-management .documents-list .item .actions {
  width: 200px;
}
.documents-management .documents-list .item .actions .spinner {
  border: 2px solid #fff;
  border-top: 2px solid #34a5dd;
  border-radius: 50%;
  width: 15px;
  height: 15px;
  animation: spin 2s linear infinite;
}
@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.documents-management .documents-list .item .actions .action {
  display: inline-flex;
  color: #9e1350;
}
.documents-management .documents-list .item .actions .action:hover {
  color: #700e39;
}
.documents-management .documents-list .tagger {
  margin: 15px;
}
.documents-management .documents-list .tagger .spacer {
  height: 200px;
  border: 0px;
}
.documents-management .documents-list .tagger .done {
  margin-top: 15px;
  width: 100%;
}
.documents-management .documents-list .tagger .entry .description {
  margin-bottom: 15px;
}
.documents-management .documents-list .tagger .documents.tags-input .ti-autocomplete {
  position: absolute;
}
.documents-management .documents.tags-input {
  width: 100%;
  max-width: inherit;
}
.documents-management .documents.tags-input .ti-input .ti-tags .ti-tag {
  background-color: #9e1350;
  font-size: inherit;
}
.documents-management .documents.tags-input .ti-input .ti-tags .ti-tag.ti-deletion-mark {
  background-color: #ff0000;
}
.documents-management .documents.tags-input .ti-autocomplete {
  overflow-y: auto;
  max-height: 200px;
  position: relative;
}
.documents-management .documents.tags-input .ti-autocomplete .ti-selected-item {
  background-color: #e84e1b;
}
